<template>
  <div class="bottom-box">
	<!---->
    <div class="progress-box" @click="setSongDot" ref="progressBox">
      <div class="progress-bar">
        <div
          class="progressed"
          :style="{
            width:
              duration === null ? 0 : (currentTime / duration) * 760 + 'px',
          }"
        ></div>
      </div>
      <div
        class="slider-dot"
        :style="{
          'margin-left':
            (duration === null ? -6 : (currentTime / duration) * 760 - 6) +
            'px',
        }"
        @mousedown="songDotDown"
        @mousemove="songDotMove"
      >
        <div class="breath-dot"></div>
      </div>
    </div>
	<!---->
    <div class="time-box">{{ showTime }}</div>
    <!---->
	<div class="speed-box">
      <div
        class="speed-text-box"
        tabindex="0"
        @click="isShowSpeedOptions = true"
        @blur="hideSpeedOptions"
      >
        <div class="speed-text">{{ speedText }}</div>
        <img src="@/assets/arrow-up.svg" />
      </div>
      <div class="speed-options-box" v-show="isShowSpeedOptions">
        <div class="speed-option" @click="changeSpeed(2, '2x')">2.0x</div>
        <div class="speed-option" @click="changeSpeed(1.5, '1.5x')">1.5x</div>
        <div class="speed-option" @click="changeSpeed(1.25, '1.25x')">
          1.25x
        </div>
        <div class="speed-option" @click="changeSpeed(1, '1.0x')">1.0x</div>
        <div class="speed-option" @click="changeSpeed(0.75, '0.75x')">
          0.75x
        </div>
        <div class="speed-option" @click="changeSpeed(0.5, '0.5x')">0.5x</div>
      </div>
    </div>
    <!---->
	<div class="play-mode-box">
      <img
        :src="modeStateImgUrl"
        tabindex="0"
        @click="isShowModeOptions = true"
        @blur="hideModeOptions"
        width="20px"
		title=""
      />
      <div class="mode-options-box" v-show="isShowModeOptions">
        <div class="mode-option" @click="changePlayMode('listForwardMode')">
          <img src="@/assets/list-forward.svg" />
          <div class="mode-option-text"></div>
        </div>
        <div class="mode-option" @click="changePlayMode('singleCycleMode')">
          <img src="@/assets/single-cycle.svg" />
          <div class="mode-option-text"></div>
        </div>
        <div class="mode-option" @click="changePlayMode('listCycleMode')">
          <img src="@/assets/list-cycle.svg" />
          <div class="mode-option-text"></div>
        </div>
        <div class="mode-option" @click="changePlayMode('randomMode')">
          <img src="@/assets/random.svg" />
          <div class="mode-option-text"></div>
        </div>
      </div>
    </div>
	<!---->
	<div class="download-box" v-if="currentList.length != 0">
	  <a :href="'https://music.163.com/song/media/outer/url?id='+currentList[currentListIndex].id+'.mp3'" target="_blank">
		 <img title="" class="download" src="@/assets/download.svg"/>
	  </a>
	</div>
    <!---->
	<div class="mark-box">
      <img
        @click="markSong"
        :src="
          currentList[currentListIndex] === null
            ? this.markImgUrl
            : setMarkImgUrl
        "
		title=""
      />
    </div>
    <!---->
	<div class="play-contral-box">
      <div class="previous-song" @click="playPreviousSong">
        <img src="@/assets/previous-song.svg" width="40px" />
      </div>
      <div class="palying-or-paused" @click="playContral">
        <img :src="playStateImgUrl" width="40px" />
      </div>
      <div class="next-song" @click="playNextSong">
        <img src="@/assets/next-song.svg" width="40px" />
      </div>
    </div>
    <!---->
	<div class="volume-box">
      <img :src="volumeStateImgUrl" width="24px" @click="mute" />
      <div class="volume-percentage">
        {{ parseInt((volumeDotX / 120) * 100) + "%" }}
      </div>
      <div
        class="volume-adjust-box"
        @click="setVolumeDot"
        ref="volumeAdjustBox"
      >
        <div class="volume-adjust-bar">
          <div
            class="volume-progressed"
            :style="{ width: volumeDotX + 'px' }"
          ></div>
        </div>
        <div
          class="slider-dot"
          :style="{ 'margin-left': volumeDotX - 6 + 'px' }"
          @mousedown="volumeDotDown"
          @mousemove="volumeDotMove"
        >
          <div class="breath-dot"></div>
        </div>
      </div>
    </div>
    
	<!---->
	<audio id="audio" :src="currentSongUrl" autoplay></audio>
	<!---->
	<div class="video_con" v-show="mvShow == true">
		<video id="video" controls="controls" :src="mvurl"></video>
		<div class="mask" @click="hideMV()"></div>
	</div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "bottom-box",
  data() {
    return {
      audio: null,
	  video:null,
      currentTime: null,
      duration: null,
      isSongDotMovable: false,
      isVolumeDotMovable: false,
      songDotX: null,
      volumeDotX: 24,
      playStateImgUrl: require("@/assets/paused.svg"),
      playingImgUrl: require("@/assets/playing.svg"),
      pausedImgUrl: require("@/assets/paused.svg"),
      volumeStateImgUrl: require("@/assets/volume.svg"),
      volumeImgUrl: require("@/assets/volume.svg"),
      volumeMuteImgUrl: require("@/assets/volume-mute.svg"),
      modeStateImgUrl: require("@/assets/list-forward.svg"),
      listForwardImgUrl: require("@/assets/list-forward.svg"),
      singleCycleImgUrl: require("@/assets/single-cycle.svg"),
      listCycleImgUrl: require("@/assets/list-cycle.svg"),
      randomImgUrl: require("@/assets/random.svg"),
      markImgUrl: require("@/assets/mark.svg"),
      markedImgUrl: require("@/assets/marked.svg"),
      isMuteNow: false,
      speedText: "",
      playSpeed: 1,
      isShowSpeedOptions: false,
      isShowModeOptions: false,
    };
  },
  computed: {
    ...mapState({
      currentList: (state) => state.currentList,
      currentListIndex: (state) => state.currentListIndex,
      currentSongUrl: (state) => state.currentSongUrl,
      markedList: (state) => state.markedList,
      playMode: (state) => state.playMode,
      isSearchInputOnFocus: (state) => state.isSearchInputOnFocus,
	  mvShow: (state) => state.mvShow,
	  mvurl: (state) => state.mvurl,
    }),
	
	// 
    showTime() {
      //
      var current_m = Math.floor(this.currentTime / 60);
      var current_s = parseInt(this.currentTime % 60);
      var duration_m = Math.floor(this.duration / 60);
      var duration_s = parseInt(this.duration % 60);

      current_m += "";
      current_s += "";
      duration_m += "";
      duration_s += "";

      current_m = current_m.length == 1 ? "0" + current_m : current_m;
      current_s = current_s.length == 1 ? "0" + current_s : current_s;
      duration_m = duration_m.length == 1 ? "0" + duration_m : duration_m;
      duration_s = duration_s.length == 1 ? "0" + duration_s : duration_s;

      return current_m + ":" + current_s + "/" + duration_m + ":" + duration_s;
    },
	
	// 
    setMarkImgUrl() {
      var isCurrentListEmpty = false;
      if (this.currentList.length !== 0) {
        //markmark
        var haveMatchedId = false;
        for (let i = 0; i < this.markedList.length; i++) {
          if (
            this.currentList[this.currentListIndex].songmid ===
            this.markedList[i].songmid
          ) {
            haveMatchedId = true;
            break;
          }
        }
      } else {
        isCurrentListEmpty = true;
      }
      return isCurrentListEmpty
        ? this.markImgUrl
        : haveMatchedId === true
        ? this.markedImgUrl
        : this.markImgUrl;
    },
  },
  watch: {
	mvShow: function () {
		if(this.mvShow == true){
			  this.audio.pause();
			  this.playStateImgUrl = this.pausedImgUrl;
			  this.$store.commit("albumRotatePaused");
		}
	},
  },
  mounted() {
    //
    if (localStorage.hasOwnProperty("volumeDotX")) {
      this.volumeDotX = parseInt(localStorage.getItem("volumeDotX"));
    }

    //audio
    var audio = document.querySelector("#audio");
    this.audio = audio;
    this.audio.volume = this.volumeDotX / 120;
    this.audio.playbackRate = this.playSpeed;
	
	//video
	var video = document.querySelector("#video");
	this.video = video;
	
    //
    this.addAudioEventListeners();
  },
  methods: {
    addAudioEventListeners() {
      this.audio.addEventListener("canplay", this.getDuration);
      this.audio.addEventListener("timeupdate", this.getCurrentTime);
      this.audio.addEventListener("ended", this.autoPlayNextSong);
    },

    mouseUp() {
      this.isSongDotMovable = false;
      this.isVolumeDotMovable = false;
      this.audio.volume = this.volumeDotX / 120;

      //
      localStorage.setItem("volumeDotX", this.volumeDotX.toString());
    },

    getDuration() {
      this.duration = this.audio.duration;
      this.playStateImgUrl = this.playingImgUrl;

      //
      this.audio.playbackRate = this.playSpeed;

      //app
      this.$store.commit("sendHaveStarted", true);

      //app
      this.$store.commit("albumRotateRunning");
    },

    getCurrentTime() {
      this.currentTime = this.audio.currentTime;
      this.$store.commit("sendCurrentTime", this.currentTime);
    },

    changeCurrentTime(value) {
      this.audio.currentTime = value;
    },

    changeSpeed(rate, text) {
      this.playSpeed = rate;
      this.audio.playbackRate = this.playSpeed;
      this.speedText = text;
      this.isShowSpeedOptions = false;
    },

    hideSpeedOptions() {
      setTimeout(() => {
        this.isShowSpeedOptions = false;
      }, 200);
    },

    changePlayMode(mode) {
      this.$store.commit("sendPlayMode", mode);
      switch (mode) {
        case "listForwardMode":
          this.modeStateImgUrl = this.listForwardImgUrl;
          break;
        case "singleCycleMode":
          this.modeStateImgUrl = this.singleCycleImgUrl;
          break;
        case "listCycleMode":
          this.modeStateImgUrl = this.listCycleImgUrl;
          break;
        case "randomMode":
          this.modeStateImgUrl = this.randomImgUrl;
          break;
      }
    },

    hideModeOptions() {
      setTimeout(() => {
        this.isShowModeOptions = false;
      }, 200);
    },

    markSong() {
      // mark
      if (this.currentList.length !== 0) {
        this.$store.commit(
          "sendMarkedSong",
          this.currentList[this.currentListIndex]
        );
      }
    },

	// 
    playPreviousSong() {
      //
      if (this.playMode === "randomMode") {
        this.$store.commit("handleRandomMode");
      } else {
        //
        this.$store.commit("playPreviousSong");
      }

      //
      //1
      setTimeout(() => {
        this.audio.playbackRate = this.playSpeed;
      }, 1000);
    },
	
	// 
    playContral() {
      // 
      if (this.currentList.length !== 0) {
        if (this.audio.paused) {
          this.audio.play();
          this.playStateImgUrl = this.playingImgUrl;
          this.$store.commit("albumRotateRunning");
        } else {
          this.audio.pause();
          this.playStateImgUrl = this.pausedImgUrl;
          this.$store.commit("albumRotatePaused");
        }
      } else {
        this.audio.pause();
        this.playStateImgUrl = this.pausedImgUrl;
        this.$store.commit("albumRotatePaused");
      }
    },

	// 
    autoPlayNextSong() {
      switch (this.playMode) {
        case "listForwardMode":
          //,
          this.$store.commit("playNextSong");
          break;

        case "singleCycleMode":
          //
          this.audio.play();
          break;

        case "listCycleMode":
          //vuexplayNextSong
          this.$store.commit("playNextSong");
          break;

        case "randomMode":
          //
          this.$store.commit("handleRandomMode");
          break;
      }

      setTimeout(() => {
        this.audio.playbackRate = this.playSpeed;
      }, 1000);
    },

	// 
    playNextSong() {
      if (this.playMode == "randomMode") {
        this.$store.commit("handleRandomMode");
      } else {
        this.$store.commit("playNextSong");
      }

      setTimeout(() => {
        this.audio.playbackRate = this.playSpeed;
      }, 1000);
    },

	// 
    songDotDown(e) {
      this.isSongDotMovable = true;
      this.audio.removeEventListener("timeupdate", this.getCurrentTime);
      this.audio.volume = 0;
    },
    songDotMove(e) {
      if (this.isSongDotMovable) {
        //progressBox
        this.songDotX =
          e.x - this.$refs.progressBox.getBoundingClientRect().left;
        if (this.songDotX < 0) {
          //
          this.songDotX = 0;
        }
        if (this.songDotX > 760) {
          //
          this.songDotX = 760;
        }
        this.audio.currentTime = (this.songDotX / 760) * this.duration;
        this.audio.addEventListener("timeupdate", this.getCurrentTime);
      }
    },
    setSongDot(e) {
      this.songDotX = e.x - this.$refs.progressBox.getBoundingClientRect().left;
      this.audio.currentTime = (this.songDotX / 760) * this.duration;
      this.audio.play();
    },
	// 
    volumeDotDown() {
      this.isVolumeDotMovable = true;
    },
    volumeDotMove(e) {
      if (this.isVolumeDotMovable) {
        //volume-adjust-box
        this.volumeDotX =
          e.x - this.$refs.volumeAdjustBox.getBoundingClientRect().left;
        if (this.volumeDotX < 0) {
          this.volumeDotX = 0;
        }
        if (this.volumeDotX > 120) {
          this.volumeDotX = 120;
        }
        this.audio.volume = this.volumeDotX / 120;
      }
    },
    setVolumeDot(e) {
      this.volumeDotX =
        e.x - this.$refs.volumeAdjustBox.getBoundingClientRect().left;
      if (this.volumeDotX < 0) {
        this.volumeDotX = 0;
      }
      if (this.volumeDotX > 120) {
        this.volumeDotX = 120;
      }
      this.audio.volume = this.volumeDotX / 120;
    },
    mute() {
      if (this.isMuteNow == false) {
        this.audio.volume = 0;
        this.lastVolumeDotX = this.volumeDotX;
        this.volumeDotX = 0;
        this.isMuteNow = true;
        this.volumeStateImgUrl = this.volumeMuteImgUrl;
      } else {
        this.volumeDotX = this.lastVolumeDotX;
        this.audio.volume = this.volumeDotX / 120;
        this.isMuteNow = false;
        this.volumeStateImgUrl = this.volumeImgUrl;
      }
    },
	// MV
	hideMV(){
		this.$store.commit('changMV',false);
		// MV
		this.video.pause()
	},
  },
};
</script>

<style>
.bottom-box {
  width: 100%;
  height: 80px;
}

.video_con video {
  position: fixed;
  width: 1000px;
  height: 546px;
  left: 50%;
  top: 50%;
  margin-top: -273px;
  transform: translateX(-50%);
  z-index: 990;
}
.video_con .mask {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 980;
  background-color: rgba(0, 0, 0, 0.8);
}

.progress-box {
  width: 100%;
  height: 12px;
  margin-top: 10px;
  position: relative;
}

.progress-box:hover {
  cursor: pointer;
}

.progress-bar {
  width: 100%;
  height: 2px;
  margin-top: 5px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2);
}

.progressed {
  height: 100%;
  background-color: white;
}

.slider-dot {
  width: 12px;
  height: 12px;
  position: absolute;
  border-radius: 50%;
  background-color: deeppink;
  box-shadow: 0 0 10px var(--highlight-deep-color);
}

.slider-dot:hover {
  cursor: pointer;
}

.slider-dot:active {
  transform: scale(1.5);
}

.breath-dot {
  width: 6px;
  height: 6px;
  margin-top: 3px;
  margin-left: 3px;
  border-radius: 50%;
  background-color: white;
}

.time-box {
  width: 80px;
  height: 50px;
  float: left;
  font-size: 13px;
  line-height: 50px;
  color: white;
}

.speed-box {
  width: 58px;
  height: 50px;
  position: relative;
  float: left;
  color: white;
}

.speed-box:hover {
  cursor: pointer;
}

.speed-text-box {
  width: 56px;
  height: 20px;
  margin-top: 15px;
  border-radius: 5px;
  border: 1px solid white;
}

.speed-text-box:focus {
  outline: none;
}

.speed-text {
  width: 30px;
  height: 20px;
  float: left;
  margin-left: 4px;
  line-height: 20px;
  text-align: center;
  font-size: 13px;
}

.speed-text-box img {
  width: 14px;
  height: 14px;
  margin-top: 3px;
  margin-left: 3px;
}

.speed-options-box {
  position: absolute;
  width: 56px;
  height: 165px;
  margin-top: -190px;
  border-radius: 8px;
  background-color: white;
  box-shadow: 0 0 10px var(--highlight-deep-color);
}

.speed-option {
  width: 50px;
  height: 20px;
  margin-left: 3px;
  margin-top: 5px;
  line-height: 20px;
  text-align: center;
  border-radius: 5px;
  color: black;
}

.speed-option:first-child {
  margin-top: 10px;
}

.speed-option:hover {
  transform: scale(1.05);
  background-color: var(--highlight-color);
}

.speed-option:active {
  transform: scale(1.1);
}

.play-mode-box {
  width: 30px;
  height: 50px;
  position: relative;
  float: left;
}

.play-mode-box > img {
  width: 20px;
  height: 20px;
  margin-top: 15px;
  margin-left: 5px;
  float: left;
}

.play-mode-box > img:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.play-mode-box > img:focus {
  outline: none;
}

.mode-options-box {
  width: 100px;
  height: 115px;
  position: absolute;
  margin-top: -105px;
  margin-left: -35px;
  border-radius: 10px;
  background-color: darkgray;
  box-shadow: 0 0 10px var(--highlight-deep-color);
}

.mode-options-box:hover {
  cursor: pointer;
}

.mode-option {
  width: 80px;
  height: 20px;
  margin-top: 5px;
  margin-left: 10px;
  border-radius: 5px;
}

.mode-option:hover {
  transform: scale(1.05);
  background-color: var(--highlight-color);
}

.mode-option:active {
  transform: scale(1.1);
}

.mode-option:first-child {
  margin-top: 10px;
}

.mode-option img {
  width: 20px;
  height: 20px;
  float: left;
  border-radius: 5px 0 0 5px;
}

.mode-option-text {
  width: 60px;
  height: 20px;
  float: left;
  line-height: 20px;
  text-align: center;
  font-size: 12px;
  border-radius: 0 5px 5px 0;
}

.download-box {
  width: 30px;
  height: 30px;
  position: relative;
  float: left;
  
}
.download{
	margin-top: 14px;
	margin-left: 2px;
	width: 25px;
	height: 25px;
	cursor: pointer;
}
.download:hover{
	transform: scale(1.1);
}

.mark-box {
  width: 30px;
  height: 50px;
  float: left;
  margin-top: 3px;
}

.mark-box > img {
  width: 20px;
  height: 20px;
  margin-top: 15px;
  margin-left: 5px;
}

.mark-box > img:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.mark-box > img:active {
  transform: scale(1.5);
}

.play-contral-box {
  width: 124px;
  height: 50px;
  float: left;
  margin-left: 115px;
  margin-top: 2px;
}

.previous-song {
  width: 40px;
  height: 40px;
  float: left;
}

.previous-song:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.previous-song:active {
  transform: scale(1.5);
}

.palying-or-paused {
  width: 40px;
  height: 40px;
  border: 2px solid white;
  border-radius: 50%;
  float: left;
}

.palying-or-paused:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.palying-or-paused:active {
  transform: scale(1.3);
}

.next-song {
  width: 40px;
  height: 40px;
  float: left;
}

.next-song:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.next-song:active {
  transform: scale(1.5);
}

.volume-box {
  width: 180px;
  height: 50px;
  float: right;
}

.volume-box img {
  float: left;
  margin-top: 13px;
}

.volume-box img:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.volume-box img:active {
  transform: scale(1.5);
}

.volume-percentage {
  width: 36px;
  height: 20px;
  float: left;
  margin-top: 15px;
  line-height: 20px;
  text-align: center;
  font-size: 10px;
  color: ghostwhite;
  font-weight: bolder;
}

.volume-adjust-box {
  width: 120px;
  height: 12px;
  float: right;
  margin-top: 19px;
}

.volume-adjust-box {
  cursor: pointer;
}

.volume-adjust-bar {
  width: 120px;
  height: 2px;
  margin-top: 5px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2);
}

.volume-progressed {
  height: 100%;
  background-color: white;
}
</style>
