<template>
	<div class="top-box">
	  <input type="text" class="search-input" v-model.trim="searchText" @keyup.enter="searchSong" placeholder="(...)" />
	</div>
</template>

<script>
export default {
  name: "top-box",
  data() {
    return {
      searchText: "",
    };
  },
  methods: {
    searchSong() {
      if (this.searchText !== "") {
        // 
        this.$router.push("SearchListPage");
        this.$store.commit("handleSearchSong", this.searchText);
      }
    }
  }
};
</script>

<style>
.top-box {
  width: 100%;
  height: 50px;
  position: relative;
}

.back-or-forward {
  height: 20px;
  margin-top: 15px;
  float: left;
}

.back-or-forward img {
  border-radius: 6px;
}

.back-or-forward img:hover {
  cursor: pointer;
  transform: scale(1.2);
  background-color: var(--highlight-color);
}

.back-or-forward img:active {
  transform: scale(1.5);
}

.back-or-forward img:nth-child(2) {
  margin-left: 10px;
}

.search-input {
  border: none;
  width: 400px;
  height: 30px;
  float: left;
  margin-left: 200px;
  margin-top: 10px;
  padding-left: 8px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 15px 15px;
  color: darkblue;
}

.search-input:focus {
  outline: 0;
  box-shadow: 0 0 5px var(--highlight-deep-color);
  background-color: rgba(255, 255, 255, 0.6);
}

.search-history-box {
  width: 405px;
  max-height: 100px;
  position: absolute;
  margin-left: 100px;
  margin-top: 50px;
  overflow-x: hidden;
  overflow-y: scroll;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 0 5px var(--highlight-deep-color);
}

.search-history-item {
  height: 20px;
  float: left;
  margin-left: 5px;
  margin-top: 10px;
  border-radius: 8px;
  box-shadow: 0 0 5px var(--highlight-deep-color);
}

.search-history-item:last-child {
  margin-bottom: 10px;
}

.search-history-item:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.search-history-item:active {
  transform: scale(1.2);
}

.search-history-block-text {
  float: left;
  margin-left: 5px;
  line-height: 20px;
  font-size: 14px;
}

.delete-this-search-text {
  width: 20px;
  height: 20px;
  float: left;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}

.delete-this-search-text:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.delete-this-search-text:active {
  transform: scale(1.5);
}
</style>
