<template>
  <div class="left-nav">
    <div class="logo-box">
	  <img src="@/assets/mylogo.png" width="100%" height="80px">
    </div>
	
	<router-link to="/SearchListPage" class="search">
	  <img class="search-icon" src="@/assets/search.svg">
	  <div class="search-text"></div>
	</router-link>
	<router-link to="/Top100Page" class="music">
	  <img class="music-icon" src="@/assets/music.svg">
	  <div class="music-text"></div>
	</router-link>
    <router-link to="/AlbumPage" class="discover">
      <img class="discover-icon" src="@/assets/album.svg">
      <div class="discover-text"></div>
    </router-link>
	<router-link to="/AlbumListPage" class="album">
	  <img class="album-icon" src="@/assets/album_list.svg">
	  <div class="album-text"></div>
	</router-link>
    <router-link to="/MarkedListPage" class="marked">
      <img class="marked-icon" src="@/assets/marked.svg">
      <div class="marked-text"></div>
    </router-link>
	
    <div class="playing-box">
      <img
        :src="getAlbumImgUrl"
        :style="
          haveStarted === true ? albumImgStyle : ''
        "
      />
      <div class="playing-song-name">
        {{
          currentList.length === 0
            ? ""
            : currentList[currentListIndex].name
        }}
      </div>
      <div class="playing-singer-name">
        {{
          currentList.length == 0
            ? ""
            : currentList[currentListIndex].artists[0].name
        }}
      </div>
	  <div class="playing-mv-name" v-if="currentList.length != 0">
		<img class="music-mv" title="MV" v-if="currentList[currentListIndex].mvid != 0" @click="openMV()" src="@/assets/mv.svg">
	  </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: "left-nav",
  data() {
    return {
      defaultAlbumImgUrl: require("@/assets/default-album-img.jpg"),
      albumImgUrl: null,
    };
  },
  computed: {
    ...mapState({
      haveStarted: state => state.haveStarted,
      albumImgStyle: state => state.albumImgStyle,
      currentList: state => state.currentList,
      currentListIndex: state => state.currentListIndex,
    }),
	// 
    getAlbumImgUrl() {
		// 
		if ( this.currentList.length === 0 || this.currentList[this.currentListIndex].album.name === "") {
			this.albumImgUrl = this.defaultAlbumImgUrl
		} else {
			var that = this
			setTimeout(function(){
				if(that.currentList[that.currentListIndex].albumimg != ''){
					that.albumImgUrl = that.currentList[that.currentListIndex].albumimg
				}else{
					that.albumImgUrl = that.defaultAlbumImgUrl
				}
			},300)
		}

		return this.albumImgUrl;
    },
  },
  methods:{
	  openMV(){
		  this.$store.commit("changMV", true);  
	  }
  }
};
</script>

