import { request } from "@/network/request.js"

// URL
export function getSongUrl(id) {
	return request({
		url: 'song/url',
		params: {
			id: id
		}
	})
}
// 
export function getSongLyric(id) {
	return request({
		url: 'lyric',
		params: {
		    id: id
		}
	})
}
// 
export function getSongAlbum(albumid) {
	return request({
		url: 'album',
		params: {
		    id: albumid
		}
	})
}
// MV
export function getSongMV(mvid) {
	return request({
		url: 'mv/url',
		params: {
			id: mvid
		}
	})
}
// 
export function getSongComment(id) {
	return request({
		url: 'comment/music',
		params: {
			id: id,
			limit: 50, // 50
			offset: 0
		}
	})
}
// 
export function getSongDetail(ids) {
	return request({
		url: 'song/detail',
		params: {
			ids: ids
		}
	})
}