<template>
  <div class="comment-page" ref="CommentPage">
    <div v-if="songComments.length != 0" class="comment-item" v-for="(comment, index) in songComments" :key="index">
      <div class="pic">
		  <img class="avatar" :src="comment.user.avatarUrl" alt=""/>
	  </div>
	  <div class="comment">
	    <div class="comment-info">
	      <div class="comment-nickname">
	        <p>{{comment.user.nickname}}</p>
	      </div>
		  <div class="comment-time">
		    <p>{{comment.time | timeFilter}}</p>
		  </div>
	    </div>
		<div class="comment-content">
		  <p>{{comment.content}}</p>
		</div>
	  </div>
    </div>
	<div v-if="songComments.length == 0">
	  <div class="tip">
	    
	  </div>
	</div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { formatDateTime } from "@/utils/time.js";

export default {
  name: "comment-page",
  data() {
    return {
		
    };
  },
  computed: {
	  ...mapState({
	    songComments:state => state.songComments
	  }),
  },
  filters:{
	timeFilter(value){
		return formatDateTime(value)
	}
  }
};
</script>

<style>
	.comment-page {
		width: 95%;
		height: 100%;
		margin-left: 5%;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	
	.comment-item{
		width: 95%;
		height: 90px;
		margin-top: 10px;
		margin-left: 0%;
		background: rgba(0,0,0,.13);
		border-radius: 10px;
		padding: 15px 10px;
		transition: 1s;
	}
	.comment-item:hover{
		background: rgba(0,0,0,.33);
	}
	.pic{
		width: 15%;
		height: 100%;
		float: left;
	}
	.avatar{
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	.comment{
		width: 85%;
		height: 100%;
		float: left;
	}
	.comment-info{
		width: 300px;
		height: 25px;
		margin-top: -5px;
	}
	.comment-nickname{
		width: 150px;
		height: 25px;
		line-height: 25px;
		float: left;
		color: ghostwhite;
		font-size: 13px;
		margin-left: 10px;
		margin-top: -5px;
	}
	.comment-time{
		width: 120px;
		height: 25px;
		line-height: 25px;
		float: left;
		margin-left: 10px;
		color: whitesmoke;
		font-size: 12px;
		margin-top: -3px;
	}
	.comment-content{
		width: 100%;
		height: 65px;
		margin-top: 15px;
		color: lightgrey;
		font-size: 12px;
		opacity: .7;
	}
	
	.tip{
		width: 80px;
		margin: 50% auto;
		color: white;
	}
</style>