<template>
	<div class="right-bar">
		<div class="switch-page">
			<img v-show="showPage == 'lyric'" title="" class="lyric-icon" src="@/assets/lyric_blur.svg" @click="changeLyric()">
			<img v-show="showPage != 'lyric'" title="" class="lyric-icon" src="@/assets/lyric.svg" @click="changeLyric()">
			
			<img v-show="showPage == 'comment'" title="" class="comment-icon" src="@/assets/comment_blur.svg" @click="changeComment()">
			<img v-show="showPage != 'comment'" title="" class="comment-icon" src="@/assets/comment.svg" @click="changeComment()">
		</div>
		<div class="page" v-if="showPage == 'lyric'">
			<lyric-page></lyric-page>
		</div>
		<div class="page" v-if="showPage == 'comment'">
			<comment-page></comment-page>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex'
import LyricPage from './lyric_page/LyricPage'
import CommentPage from './comment_page/CommentPage'

export default {
  name: 'right-bar',
  components: {
	LyricPage,
	CommentPage
  },
  data() {
    return {
  		showPage: 'lyric',
    };
  },
  methods: {
  	  changeLyric(){
		  if(this.showPage != 'lyric'){
			  this.showPage = 'lyric'
		  }
	  },
	  changeComment(){
		  if(this.showPage != 'comment'){
			  this.showPage = 'comment'
			  this.$store.commit('getSongComment')  
		  }
	  }
  },
}
</script>

<style>
	.right-bar {
	  width: 450px;
	  height: 90%;
	  float: left;
	  position: relative;
	  border-radius: 20px 0 0 20px;
	  margin-top: 5%;
	}
	
	.switch-page{
		width: 100px;
		height: 5%;
		margin: 0 auto;
	}
	.lyric-icon {
	  width: 25px;
	  height: 25px;
	  float: left;
	  cursor: pointer;
	  margin-top: -3px;
	}
	.lyric-icon:hover {
	  transform: scale(1.2);
	}
	.comment-icon{
		width: 20px;
		height: 20px;
		float: left;
		cursor: pointer;
		margin-left: 20px;
	}
	.comment-icon:hover {
	  transform: scale(1.2);
	}
	
	.page{
		width: 100%;
		height: 85%;
	}
</style>