<template>
  <div class="lyric-page" ref="lyricPage">
    <div v-if="lyricTextArr.length != 0" class="lyric-item" v-for="(item, index) in lyricTextArr" :key="index">
      <div class="lyric-text-line" :style="setplayingLyricStyle(item, index)">
        {{ item.text }}
      </div>
    </div>
	<div v-if="lyricTextArr.length == 0" class="lyric-item">
	  <div class="tip">
		  
	  </div>
	</div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "lyric-page",
  data() {
    return {
      playingLyricStytle: {
        transform: "scale(1.3)",
        color: "darkorange",
		fontWeight:"bolder"
      },
      currentLyricIndex: 0,
      scrollTop: 0,
    };
  },
  computed: {
    ...mapState({
      lyricTextArr: (state) => state.lyricTextArr,
      currentTime: (state) => state.currentTime,
    }),
  },
  watch: {
    // 
    currentLyricIndex: function () {
      this.$refs.lyricPage.scrollTop = 0;
      if (this.currentLyricIndex > 15) {
        this.$refs.lyricPage.scrollTop = 40 * (this.currentLyricIndex - 15);
      }
    },
  },
  methods: {
    setplayingLyricStyle(item, index) {
      // 
      if (
        this.currentTime > item.time &&
        (index === this.lyricTextArr.length - 1
          ? true
          : this.currentTime < this.lyricTextArr[index + 1].time)
      ) {
        this.currentLyricIndex = index;
        return this.playingLyricStytle;
      }
    },
  },
};
</script>

<style>
.lyric-page {
  width: 95%;
  height: 100%;
  margin-left: 5%;
  overflow-x: hidden;
  overflow-y: scroll;
}

.lyric-text-line {
  margin-top: 10px;
  height: 20px;
  color: white;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.tip{
	width: 80px;
	margin: 50% auto;
	color: white;
}
</style>