import Vue from 'vue'
import Vuex from 'vuex'

import { Message } from 'element-ui';
Vue.use(Vuex)

// 
import { request } from "@/network/request.js";
// 
import getSearchList from '@/network/searchRequest.js'
// 
import getRecommendMusic from '@/network/musicRequest.js'
// 
import {getRecommendAlbum, getAlbumContent} from '@/network/albumRequest.js'
// 
import {getHotMusicSheet, getRecommendMusicSheet, getMusicSheetDetail} from '@/network/musicSheetRequest.js'
// 
import {getSongUrl, getSongLyric, getSongAlbum, getSongMV, getSongComment, getSongDetail} from '@/network/songRequest.js'

export default new Vuex.Store({
  state: {
    currentList: [],
    currentListIndex: null,
    searchList: [],
    top100List: [],
    markedList: [],
	newAlbums: [],
	albumMusicList: [],
    isPlayingMarkedListSong: false,
    currentSongUrl: '',
    playMode: 'listForwardMode',
    haveStarted: false,
    albumImgStyle: {},
    haveSearched: false,
    searchText: '',
    currentPage: 0,
    loadMoreText: '',
    canLoadMore: true,
    lyricTextArr: [],
	songComments: [],
    currentTime: null,
    isSearchInputOnFocus: false,
	mvShow:false,
	mvurl:'',
  },
  mutations: {
    getHistoryData(state) {
      if (localStorage.hasOwnProperty('markedList')) {
        state.markedList = JSON.parse(localStorage.getItem('markedList'))
      }
    },

    sendCurrentIndex(state, payload) {
      state.currentListIndex = payload.index
      switch (payload.activatedPage) {
        case 'searchListPage':
          state.currentList = state.searchList
          state.isPlayingMarkedListSong = false
          break

        case 'top100Page':
          state.currentList = state.top100List
          state.isPlayingMarkedListSong = false
          break

        case 'markedListPage':
          state.currentList = state.markedList
          state.isPlayingMarkedListSong = true
          break
		  
		case 'albumListPage':
		  state.currentList = state.albumMusicList
		  state.isPlayingMarkedListSong = true
		  break		
      }
    },

	// MV
	changMV(state, payload){
		if(payload == true){
			getSongMV(state.currentList[state.currentListIndex].mvid)
				.then(res => {
				  state.mvurl = res.data.data.url
				  state.mvShow = payload
				}).catch(err => {
				  Message.error({
					message: 'MV',
					showClose: true
				  })
				})
		}else{
			state.mvShow = payload
		}
	},
	// 
	getSongComment(state){
		if(state.currentList.length != 0 && state.currentListIndex != null){
			getSongComment(state.currentList[state.currentListIndex].id)
			  .then(res => {
				state.songComments = res.data.comments
			  }).catch(err => {
				Message.error({
				  message: '',
				  showClose: true
				})
			  })
		}
	},

	// 
    sendPlayMode(state, mode) {
      state.playMode = mode
    },

	// 
    sendHaveStarted(state, haveStarted) {
      state.haveStarted = haveStarted
    },
	
	// 
    sendCurrentTime(state, currentTime) {
      state.currentTime = currentTime
    },

	// 
    sendMarkedSong(state, item) {
      //mark
      if (state.markedList.length === 0) {
        state.markedList.push(item)
      } else {

        //markmarkmark
        var haveSameItem = false
        for (let i = 0; i < state.markedList.length; i++) {
          if (state.markedList[i].songmid === item.songmid) {
            state.markedList.splice(i, 1)
            haveSameItem = true

            //mark
            if (state.isPlayingMarkedListSong === true) {

              //mark
              state.currentListIndex -= 1

              //mark
              if (state.currentListIndex < 0) {
                state.currentListIndex = 0
              }
              if (state.currentList.length !== 0) {
                this.commit('getSongUrlAndLyric')
              }
            }
            break
          }
        }
        //mark
        if (haveSameItem === false) {
          state.markedList.unshift(item)
        }
      }
      localStorage.setItem('markedList', JSON.stringify(state.markedList))
    },

    //
    albumRotateRunning(state) {
      state.albumImgStyle = {
        'animation': 'albumRotate 20s linear infinite running'
      }
    },

    //
    albumRotatePaused(state) {
      state.albumImgStyle = {
        'animation': 'albumRotate 20s linear infinite paused'
      }
    },

	// 
    handleTop100List(state) {
      getRecommendMusic()
		  .then(res => {
			let music_list = res.data.result
			let song_list = []
			if(music_list.length != 0){
				music_list.forEach((item) => {
					song_list.push(item.song)
				})
			}
			state.top100List = song_list
		  }).catch(err => {
			Message.error({
			  message: '',
			  showClose: true
			})
		  })
    },
	
	// 
	handleHotAlbumList(state){
		getRecommendAlbum()
		  .then(res => {
			  state.newAlbums = res.data.albums
		  }).catch(err => {
			Message.error({
			  message: '',
			  showClose: true
			})
		  })
	},
	
	// 
	getAlbumMusic(state, albumid){
		getAlbumContent(albumid)
		  .then(res => {
			  let songs = res.data.songs
			  let album_songs = []
			  songs.forEach((item) => {
				  let album_song = {
					  id: item.id,
					  name: item.name,
					  mvid: item.mv,
					  album:{
						  name: item.al.name,
						  id: item.al.id
					  },
					  artists:[
						  {
							  name: item.ar[0].name
						  }
					  ]
				  }
				  
				  album_songs.push(album_song)
			  })
			  
			  state.albumMusicList = album_songs
		  }).catch(err => {
			Message.error({
			  message: '',
			  showClose: true
			})
		  })
	},

	// 
    handleSearchSong(state, searchText) {
      state.searchText = searchText
      getSearchList(state.searchText, 0)
        .then(res => {
          state.searchList = res.data.result.songs;
          // loadMore
          state.haveSearched = true
        })
        .catch(err => {
          Message.error({
            message: '',
            showClose: true
          })
        })
    },
	
	// 
    handleLoadMore(state) {
      if (state.canLoadMore) {
        state.currentPage += 1
        getSearchList(state.searchText, state.currentPage)
          .then(res => {
            //
            var moreSearchList = res.data.result.songs

            if (moreSearchList.length < 30) {
              state.loadMoreText = ""
              state.searchList.push(...res.data.result.songs)
              state.canLoadMore = false
            } else {
              state.searchList.push(...res.data.result.songs)
            }
          })
          .catch(err => {
            Message.error({
              message: '',
              showClose: true
            })
          })
      }
    },

	// url
    getSongUrlAndLyric(state) {
      // 
	  getSongUrl(state.currentList[state.currentListIndex].id)
		  .then(res => {
			//
			//state.currentSongUrl = res.data.data[0].url
			state.currentSongUrl = "https://music.163.com/song/media/outer/url?id=" + state.currentList[state.currentListIndex].id + ".mp3"
			if (state.currentSongUrl == "") {
			  Message.error({
				message: '',
				showClose: true
			  })
			  this.commit('albumRotatePaused')
			}
		  }).catch(err => {
			Message.error({
			  message: '',
			  showClose: true
			})
		  })
	  
	  // 
	  getSongLyric(state.currentList[state.currentListIndex].id)
		  .then(res => {
			//
			var lyricText = res.data.lrc.lyric
			var lyricTextArr = lyricText.split('\n')
		  
			//
			// lyricTextArr.splice(0, 5)
			//
			state.lyricTextArr = []
			//
			lyricTextArr.forEach(eachLine => {
			  var t = eachLine.substring(eachLine.indexOf("[") + 1, eachLine.indexOf("]"))
			  var lyricTextLineObj = {
				time: (t.split(":")[0] * 60 + parseFloat(t.split(":")[1])).toFixed(2),
				text: eachLine.substring(eachLine.indexOf("]") + 1, eachLine.length)
			  }
		  
			  state.lyricTextArr.push(lyricTextLineObj)
			})
		  }).catch(err => {
			Message.error({
			  message: '',
			  showClose: true
			})
		  })
	  
	  // 
	  getSongAlbum(state.currentList[state.currentListIndex].album.id)
		  .then(res => {
			state.currentList[state.currentListIndex].albumimg = res.data.album.picUrl
		  }).catch(err => {
			state.currentList[state.currentListIndex].albumimg = require("@/assets/default-album-img.jpg")
		  })
    },
	
	// 
    playPreviousSong(state) {
      //index
      state.currentListIndex -= 1
      if (state.currentListIndex >= 0) {
        this.commit('getSongUrlAndLyric')
      } else {
        state.currentListIndex = 0
      }
    },
	
	// 
    playCurrentSong() {
      this.commit('getSongUrlAndLyric')
    },
	
	// 
    playNextSong(state) {
      //index
      state.currentListIndex += 1
      if (state.currentListIndex < state.currentList.length) {
        this.commit('getSongUrlAndLyric')
      } else {
        //indexindex
        if (state.playMode === "listCycleMode") {
          state.currentListIndex = 0
          this.commit('getSongUrlAndLyric')
        } else {
          //index
          state.currentListIndex = state.currentList.length - 1
          this.commit('albumRotatePaused')
        }
      }
    },
	
	// 
	handleRandomMode(state) {
	  var randomIndex = Math.floor(Math.random() * state.currentList.length)
	  state.currentListIndex = randomIndex
	  this.commit('getSongUrlAndLyric')
	},
  },
  actions: {
	  
  },
  modules: {
	  
  }
})
