import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const SearchListPage = () => import('@/components/middle_box/router_views/search_list_page/SearchListPage')
const Top100Page = () => import('@/components/middle_box/router_views/top100_page/Top100Page')
const MarkedListPage = () => import('@/components/middle_box/router_views/marked_list_page/MarkedListPage')
const AlbumPage = () => import('@/components/middle_box/router_views/album100_page/AlbumPage')
const AlbumListPage = () => import('@/components/middle_box/router_views/album_list_page/AlbumListPage')

const routes = [
  {
    path: '/Top100Page',
    component: Top100Page,
  },
  {
    path: '/SearchListPage',
    component: SearchListPage
  },
  {
    path: '/MarkedListPage',
    component: MarkedListPage
  },
  {
    path: '/AlbumPage',
    component: AlbumPage 
  },
  {
    path: '/AlbumListPage',
    component: AlbumListPage
  },
]

const router = new VueRouter({
  routes,
})

export default router
