<template>
  <div id="app">
    <div class="app-background" :style="getBgStyle"></div>
    <div class="app-background-above">
      <left-nav></left-nav>
      <middle-box></middle-box>
      <right-nav></right-nav>
    </div>
  </div>
</template>

<script>

import LeftNav from './components/left_nav/LeftNav'
import MiddleBox from './components/middle_box/MiddleBox'
import RightNav from './components/right_nav/RightNav'

export default {
  name: 'app',
  components: {
    LeftNav,
    MiddleBox,
    RightNav
  },
  data() {
    return {
      defaultAlbumImgUrl: require("@/assets/default-album-img.jpg"),
      albumImgUrl: null,
      bgStyle: {}
    }
  },
  computed: {
    getBgStyle() {
      // 
      if(this.$store.state.currentList.length == 0 || this.$store.state.currentList[this.$store.state.currentListIndex].album == "") {
        this.albumImgUrl = this.defaultAlbumImgUrl
      }else {
		var that = this
		setTimeout(function(){
			if(that.$store.state.currentList[that.$store.state.currentListIndex].albumimg != ''){
				that.albumImgUrl = that.$store.state.currentList[that.$store.state.currentListIndex].albumimg
			}else{
				that.albumImgUrl = that.defaultAlbumImgUrl
			}
		},300)
      }
	  
      setTimeout(() => {
        this.bgStyle = {
          'background-image': 'url(' + this.albumImgUrl + ')'
        }
      },1500)
      
      return this.bgStyle
    }
  },
  mounted() {
    this.$store.commit('getHistoryData')
    this.$router.push('Top100Page')
  }
}
</script>

<style>
:root {
  --highlight-color: #c5b5f0;
  --highlight-deep-color: #7E57C2;
}

#app {
  width: 1450px;
  height: 750px;
  position: relative;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 20px;
  -webkit-user-select: none;
  box-shadow: 0 0 15px 15px gray;
}
.app-background {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -5;
  border-radius: 20px;
  background-image: url("./assets/default-album-img.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: blur(50px) brightness(60%);
  -moz-filter: blur(50px) brightness(60%);
  -o-filter: blur(50px) brightness(60%);
  -ms-filter: blur(50px) brightness(60%);
  filter: blur(50px) brightness(60%);
}
.app-background-above {
  width: 100%;
  height: 100%;
  position: absolute;
}

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(255,255,255,0.4);
}
::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: rgba(255,255,255,0.1);
}
</style>
